import { Droppable, Draggable, DragDropContext } from "react-beautiful-dnd";
import { useSelector } from "react-redux";
import { selectors } from '../airbags/airbagSlice';
import airbagTypes from "./airbagTypes";
import '../CardView.css';


export default function AirbagsDraggable() {
    // const id = useSelector(selectors.selectId);

    return (
        <div>
            {
                airbagTypes.map((airbagType, index) =>
                (
                    <Draggable key={airbagType.id} draggableId={airbagType.id} index={index}>
                        {(provided) => (
                            <div className="dragging" ref={provided.innerRef} {...provided.draggableProps} {...provided.dragHandleProps}>
                                {airbagType.title}
                            </div>
                        )}

                    </Draggable>
                )
                )
            }
        </div>

    )
}