import { v4 } from "uuid";
import frontairbags from './two_airbags.jpg'
import rearairbags from './rear_airbags.jpg'
import twoairbags from './two_airbags.jpg'

const airbagTypes = [
    {
        id: v4(),
        title: 'Front airbags',
        image: frontairbags
    },
    {
        id: v4(),
        title: 'Front and Rear airbags',
        image: rearairbags
    },
    {
        id: v4(),
        title: 'Front, Rear and Side airbags',
        image: twoairbags
    }
]

export const defaultAirbagType = airbagTypes[0];

export default airbagTypes;