import { Droppable, Draggable, DragDropContext } from "react-beautiful-dnd";
import { useSelector } from "react-redux";
import { selectors } from '../bodyframes/bodyframesSlice';
import '../CardView.css';
import bodyFramesTypes from "./bodyframesTypes";


export default function BodyFrameDraggable() {
    // const id = useSelector(selectors.selectId);

    return (
        <div>
            {
                bodyFramesTypes.map((bodyFrameType, index) =>
                (
                    <Draggable key={bodyFrameType.id} draggableId={bodyFrameType.id} index={index}>
                        {(provided) => (
                            <div className="dragging" ref={provided.innerRef} {...provided.draggableProps} {...provided.dragHandleProps}>
                                {bodyFrameType.title}
                            </div>
                        )}

                    </Draggable>
                )
                )
            }
        </div>
    )
}