import { configureStore } from '@reduxjs/toolkit';
import counterReducer from '../features/counter/counterSlice';
import wheelsReducer from '../features/wheels/wheelsSlice';
import airbagsReducer from '../features/airbags/airbagSlice';
import bodyframesReducer from '../features/bodyframes/bodyframesSlice';
import infotainmentReducer from '../features/infotainment/infotainmentSlice';
import spoilersReducer from '../features/spoilers/spoilersSlice';

export const store = configureStore({
  reducer: {
    counter: counterReducer,
    wheels: wheelsReducer,
    airbags: airbagsReducer,
    bodyframes: bodyframesReducer,
    infotainment: infotainmentReducer,
    spoilers: spoilersReducer,
  },
});
