import { useSelector, useDispatch } from 'react-redux';
import { updateAirbags, selectors } from './airbagSlice';
import airbagTypes from './airbagTypes';
import '../CardView.css';

export default function Airbags() {
    const dispatch = useDispatch();
    const id = useSelector(selectors.selectId);

    const listItems = airbagTypes.map((airbagType) =>
        <button key={airbagType.id} className={id === airbagType.id ? "airbag selected card-view" : "airbag card-view"}  onClick={() => {
            dispatch(updateAirbags(airbagType))
        }}>
            <div className="title">
                {airbagType.title}
            </div>
            <img src={airbagType.image} alt="" />
        </button>

    )

    return (
        <div className="airbags_wrapper">
            <div className="label">
                Airbags
            </div>
            <div className="airbags_choices choices">
                {listItems}
            </div>
        </div>
    )
}