import {createSlice} from '@reduxjs/toolkit';
import {defaultWheelType} from './wheelTypes';
export const wheelsSlice = createSlice ({
    name: 'wheels',
    initialState: {
        id: defaultWheelType.id,
        title: defaultWheelType.title,
    },
    reducers: {
        updateWheels: (state, action) => {
            //same logic to add new wheels or updating wheels choice
            state.title = action.payload.title;
            state.id = action.payload.id;
        },
        defaultWheels: state => {
            state.title = defaultWheelType.title;
            state.id = defaultWheelType.id;
        }
    }
})

export const selectTitle = (state) => state.wheels.title;

export const selectId = (state) => state.wheels.id;


const selectors = {
    selectTitle,
    selectId,
}

export {selectors};


export const {updateWheels, defaultWheels} = wheelsSlice.actions;



export default wheelsSlice.reducer;