import { Droppable, Draggable, DragDropContext } from "react-beautiful-dnd";
import { useDispatch, useSelector } from "react-redux";
import { selectors, addSpoilers } from '../spoilers/spoilersSlice';
import spoilerTypes from "./spoilerTypes";
import '../CardView.css';


export default function SpoilersDraggable(props) {
    return ( 
        <div>
            {
                spoilerTypes.map((spoilerType, index)=>
                    (
                        <Draggable key={spoilerType.id} draggableId={spoilerType.id} index={index}>
                        {(provided) => (
                            <div className="dragging" ref ={provided.innerRef} {...provided.draggableProps} {...provided.dragHandleProps}>
                                {spoilerType.title}
                            </div>
                        )}

                        </Draggable>
                    )
                )
            }
        </div>
                      
    )
}