import {createSlice} from '@reduxjs/toolkit';

export const infotainmentSlice = createSlice ({
    name: 'infotainment',
    initialState: {
        id: null,
        title: '',
    },
    reducers: {
        addInfotainment: (state, action) => {
            state.title = action.payload.title;
            state.id = action.payload.id;
        },
        removeInfotainment: (state) => {
            state.title = '';
            state.id = null;
        }
    }
})

export const selectTitle = (state) => state.infotainment.title;

export const selectId = (state) => state.infotainment.id;

const selectors = {
    selectTitle,
    selectId
}

export {selectors};

export const {addInfotainment, removeInfotainment} = infotainmentSlice.actions;

export default infotainmentSlice.reducer;