import { createSlice } from '@reduxjs/toolkit';
import { defaultAirbagType } from './airbagTypes';

export const airbagSlice = createSlice({
    name: 'airbags',
    initialState: {
        id: defaultAirbagType.id,
        title: defaultAirbagType.title,
    },
    reducers: {
        updateAirbags: (state, action) => {
            state.title = action.payload.title;
            state.id = action.payload.id;
        },
        defaultAirbags: state => {
            state.title = defaultAirbagType.title;
            state.id = defaultAirbagType.id;
        }
    }
})

export const selectTitle = (state) => state.airbags.title;

export const selectId = (state) => state.airbags.id;


const selectors = {
    selectTitle,
    selectId,
}

export { selectors };


export const { updateAirbags, defaultAirbags } = airbagSlice.actions;



export default airbagSlice.reducer;