import {useSelector, useDispatch} from 'react-redux';
import {addSpoilers, removeSpoilers, selectors} from './spoilersSlice';
import '../CardView.css';
import spoilerTypes from './spoilerTypes';

export default function Spoilers() {
    const dispatch = useDispatch();
    const id = useSelector(selectors.selectId);
    
    const listItems = spoilerTypes.map((spoilerType) =>
        <button key={spoilerType.id} className={id === spoilerType.id ? "spoilers selected card-view" : "spoilers card-view"} onClick={()=> {
            dispatch(addSpoilers(spoilerType))
        }}>
            <div className="title">
                {spoilerType.title}
            </div>
            <img src={spoilerType.image} alt="" />
        </button>
    );

    return (
        <div className="spoilers_wrapper">
            <div className="label">
                Spoilers
            </div>
            <div className="spoilers_choices choices">
                {listItems}
            <button className="remove-button" onClick={()=>{
                dispatch(removeSpoilers())
            }}>Remove Spoilers</button>
            </div>
        </div>
    );

}