import {useSelector, useDispatch} from 'react-redux';
import { addInfotainment, removeInfotainment, selectors } from './infotainmentSlice';
import infotainmentTypes from './infotainmentTypes';
import '../CardView.css';


export default function Infotainment() {
    const dispatch = useDispatch();
    const id = useSelector(selectors.selectId);

    const listItems = infotainmentTypes.map((infotainmentType) => 
        <button key={infotainmentType.id} className={id === infotainmentType.id ? "infotainment selected card-view" : "infotainment card-view" }onClick={()=>{
            dispatch(addInfotainment(infotainmentType))
        }}>
            <div className="title">
                {infotainmentType.title}
            </div>
            <img src={infotainmentType.image} alt="" />
        </button>

    )

    return (
        <div className="infotainment_wrapper">
            <div className="label">
                Infotainment
            </div>
            <div className="infotainment_choices choices">
                {listItems}
                <button className="remove-button" onClick={()=> {
                    dispatch(removeInfotainment())
                }}>Remove Infotainment</button>
            </div>
        </div>
    )
}