import { v4 } from "uuid";
import carbonfibre from './carbonfibre.jpg'
import steelcar from './steelcar.jpg'

const bodyFramesTypes = [
    {
        id: v4(),
        title: 'Carbon-fibre body frame',
        image: carbonfibre
    },
    {
        id: v4(), 
        title: 'Steel body frame',
        image: steelcar
    },
]

const defaultBodyFrameType = bodyFramesTypes[1];

export {defaultBodyFrameType};

export default bodyFramesTypes;