import { v4 } from "uuid";
import normalTyres from './normalTyres.jpg'
import rainTyres from './rainTyres.jpg'
import offroadTyres from './offroadTyres.jpg'
import raceTyres from './raceTyres.jpg'

const wheelTypes = [
    {
        id: v4(),
        title: 'Daily Use Wheels',
        image: normalTyres
    },
    {
        id: v4(),
        title: 'Rain Wheels',
        image: rainTyres
    },
    {
        id: v4(), 
        title: 'Racing Wheels', 
        image: raceTyres
    },
    {
        id: v4(),
        title: 'OffRoad Wheels',
        image: offroadTyres
    }
]

const defaultWheelType = wheelTypes[0];

export {defaultWheelType};

export default wheelTypes;