import './DescriptionPage.css';
import mustang from './ford_mustang.jpg'

function DescriptionPage(){
    return (
        <div className="product_wrapper">
            <img className="product-img" src={mustang} alt="Ford Mustang Shelby GT500" />
            <div className="product-title">Ford Mustang Shelby GT500</div>
            <div className="product-details">The 2021 Ford Mustang Shelby GT500 boasts 760 horses of supercharged-V-8 mayhem, but it also possesses the athleticism of smaller and lighter sports cars. Those accolades make it the most powerful production car Ford has ever built as well as the most immersive Mustang we've ever driven, which is why it's on our Editors' Choice list. At the center of the excitement is the Shelby's supercharged 5.2-liter V-8, which plays a thrilling soundtrack through its bazooka-like exhaust pipes. A manual transmission isn't available, but the seven-speed dual-clutch automatic operates damn near telepathically. The rear-drive-only coupe also accelerates so tenaciously that it should include a warranty on underwear. The GT500 feels equally thrilling when running down more exotic metal on a racetrack. Sure, it has terrible fuel economy and costs a lot for a Mustang–especially with the exorbitantly priced Carbon Fiber Track package–but the 2021 Shelby GT500 is a magnificent muscle car and a phenomenal sports car.</div>
        </div>
    )
}

export default DescriptionPage;