import {useSelector, useDispatch} from 'react-redux';
import {updateWheels, selectors} from '../wheels/wheelsSlice';
import wheelTypes from './wheelTypes';
import '../CardView.css';


export default function Wheels(){
    const dispatch = useDispatch();
    const id = useSelector(selectors.selectId);

    const listItems = wheelTypes.map((wheelType) => 
        <button key={wheelType.id} className={id === wheelType.id ? "wheels selected card-view" : "wheels card-view"} onClick={()=>{
            dispatch(updateWheels(wheelType))
        }}>
            <div className="title">
                {wheelType.title}
            </div>
            <img src={wheelType.image} alt="" />
        </button>
    );

    return (
        <div className="wheels_wrapper" >
            <div className="label">
                Wheels
            </div>
            <div className="wheels_choices choices">
               {listItems}
            </div>
        </div>
    );
}