import {createSlice} from '@reduxjs/toolkit';
import {defaultBodyFrameType} from './bodyframesTypes';

export const bodyFramesSlice = createSlice({
    name: 'bodyframes',
    initialState: {
        id: defaultBodyFrameType.id,
        title: defaultBodyFrameType.title,
    },
    reducers: {
        updateBodyFrames: (state, action) => {
            state.id = action.payload.id;
            state.title = action.payload.title;
        },
        defaultBodyFrames: state => {
            state.id = defaultBodyFrameType.id;
            state.title = defaultBodyFrameType.title;
        }
    }
})

export const selectTitle = (state) => state.bodyframes.title;

export const selectId = (state) => state.bodyframes.id;

const selectors = {
    selectTitle,
    selectId
}

export {selectors};

export const {updateBodyFrames, defaultBodyFrames} = bodyFramesSlice.actions;

export default bodyFramesSlice.reducer;