import { Droppable, Draggable, DragDropContext } from "react-beautiful-dnd";
import { useSelector } from "react-redux";
import { selectors } from '../infotainment/infotainmentSlice';
import infotainmentTypes from "./infotainmentTypes";
import '../CardView.css';


export default function InfotainmentDraggable() {
    // const id = useSelector(selectors.selectId);

    return (
        <div>
            {
                infotainmentTypes.map((infotainmentType, index) =>
                (
                    <Draggable key={infotainmentType.id} draggableId={infotainmentType.id} index={index}>
                        {(provided) => (
                            <div className="dragging" ref={provided.innerRef} {...provided.draggableProps} {...provided.dragHandleProps}>
                                {infotainmentType.title}
                            </div>
                        )}

                    </Draggable>
                )
                )
            }
        </div>

    )
}