import {useSelector, useDispatch} from 'react-redux';
import {updateBodyFrames,  selectors} from './bodyframesSlice';
import bodyFramesTypes from './bodyframesTypes';
import '../CardView.css';


export default function BodyFrames(){
    const dispatch = useDispatch();
    const id = useSelector(selectors.selectId);

    const listItems = bodyFramesTypes.map((bodyFramesType) => 
        <button key={bodyFramesType.id} className={id === bodyFramesType.id ? "bodyframes selected card-view" : "bodyframes card-view"} onClick={()=>{
            dispatch(updateBodyFrames(bodyFramesType))
        }}>
            <div className="title">
                {bodyFramesType.title}
            </div>
            <img src={bodyFramesType.image} alt="" />
        </button>
    )

    return (
        <div className="bodyframes_wrapper">
            <div className="label">
                BodyFrames
            </div>
            <div className="bodyframes_choices choices">
                {listItems}
            </div>
        </div>
    );
}