import { v4 } from "uuid";
import biginchscreen from './15inchscreen.jpg'
import smallinchscreen from './12inchscreen.jpg'

const infotainmentTypes = [
    {
        id: v4(),
        title: '12 inch screen infotainment system',
        image: smallinchscreen
    },
    {
        id: v4(),
        title: '15 inch screen infotainment system',
        image: biginchscreen
    }
]

