import {v4} from 'uuid';
import highSpoilers from './high_spoilers.jpg';
import lowSpoilers from './low_spoilers.jpg'

const spoilerTypes = [
    {
        id: v4(),
        title: 'high-wing',
        image: highSpoilers
    },
    {
        id: v4(),
        title: 'low-wing',
        image: lowSpoilers
    }
]

export default spoilerTypes;