import React from 'react';
import {useSelector, useDispatch} from 'react-redux';
// import logo from './logo.svg';
// import { Counter } from './features/counter/Counter';
import Wheels from './features/wheels/Wheels';
import WheelsDraggable from './features/wheels/WheelsDraggable';
import {selectors as wheelSelector, updateWheels} from './features/wheels/wheelsSlice';
import Airbags from './features/airbags/Airbags';
import AirbagsDraggable from './features/airbags/AirbagsDraggable';
import {selectors as airbagSelector, updateAirbags} from './features/airbags/airbagSlice'; 
import Spoilers from './features/spoilers/Spoilers';
import SpoilersDraggable from './features/spoilers/SpoilersDraggable'
import {selectors as spoilerSelector, addSpoilers} from './features/spoilers/spoilersSlice';
import Infotainment from './features/infotainment/Infotainment';
import InfotainmentDraggable from './features/infotainment/InfotainmentDraggable';
import {addInfotainment, selectors as infotainmentSelector} from './features/infotainment/infotainmentSlice';
import BodyFrames from './features/bodyframes/BodyFrames';
import BodyFrameDraggable from './features/bodyframes/BodyFrameDraggable';
import {selectors as bodyframeSelector, updateBodyFrames} from './features/bodyframes/bodyframesSlice';
import {BrowserRouter as Router, Switch, Route, Link} from 'react-router-dom';
import DescriptionPage from './DescriptionPage';
import './App.css';
import { DragDropContext, Droppable } from 'react-beautiful-dnd';

import spoilerTypes from "./features/spoilers/spoilerTypes";
import wheelTypes from "./features/wheels/wheelTypes";
import airbagTypes from "./features/airbags/airbagTypes";
import infotainmentTypes from "./features/infotainment/infotainmentTypes";
import bodyFramesTypes from "./features/bodyframes/bodyframesTypes";

function App() {
  const wheelTitle = useSelector(wheelSelector.selectTitle);
  const airbagTitle = useSelector(airbagSelector.selectTitle);
  const spoilerTitle = useSelector(spoilerSelector.selectTitle);
  const infotainmentTitle = useSelector(infotainmentSelector.selectTitle);
  const bodyframesTitle = useSelector(bodyframeSelector.selectTitle);

  const dispatch = useDispatch();
  const handleDragEnd = (data) => {
    const destination = data.destination;
    const source = data.source;
    if (!destination) {
      return
    }

    if (destination.index === source.index && destination.droppableId === source.droppableId) {
      return
    }
    // console.log(data);
    for (let i=0; i<spoilerTypes.length; i++){
      if (data.draggableId === spoilerTypes[i].id){
        dispatch(addSpoilers(spoilerTypes[i]));
        console.log('spoiler added')
      }
    }
    for (let i = 0; i < wheelTypes.length; i++) {
      if (data.draggableId === wheelTypes[i].id) {
        dispatch(updateWheels(wheelTypes[i]));
      }
    }
    for (let i = 0; i < airbagTypes.length; i++) {
      if (data.draggableId === airbagTypes[i].id) {
        dispatch(updateAirbags(airbagTypes[i]));
      }
    }
    for (let i = 0; i < infotainmentTypes.length; i++) {
      if (data.draggableId === infotainmentTypes[i].id) {
        dispatch(addInfotainment(infotainmentTypes[i]));
      }
    }
    for (let i = 0; i < bodyFramesTypes.length; i++) {
      if (data.draggableId === bodyFramesTypes[i].id) {
        dispatch(updateBodyFrames(bodyFramesTypes[i]));
      }
    }
    

  }


  return (
    <Router>
      <Switch>
        <Route exact={true} path="/">
          <div className="description">
            <DescriptionPage></DescriptionPage>
            <Link className="nice-button" to="/assemble">Start</Link>
          </div>
        </Route> 
        <Route path="/assemble">
          <div className="assemble">
            <Wheels></Wheels>
            <Airbags></Airbags>
            <Spoilers></Spoilers>
            <Infotainment></Infotainment>
            <BodyFrames></BodyFrames>
          </div>
            <div className="assembled-label label">
              You have selected
            </div>
          <div className="assembled-product">
            {wheelTitle && <div className="color-card">
              {wheelTitle}
            </div>}
            {airbagTitle && <div className="color-card">
              {airbagTitle}
            </div>}
            {spoilerTitle && <div className="color-card">
              {spoilerTitle}
            </div>}
            {infotainmentTitle && <div className="color-card">
              {infotainmentTitle}
            </div>}
            {bodyframesTitle && <div className="color-card">
              {bodyframesTitle}
            </div>}
          </div>
          <Link className="nice-button" to="/dragdrop">
            Next
          </Link>
        </Route>
        <Route path="/dragdrop">
          <div className="dragdrop">
          <div className="assembled-product-vertical">
            {wheelTitle && <div className="color-card">
              {wheelTitle}
            </div>}
            {airbagTitle && <div className="color-card">
              {airbagTitle}
            </div>}
            {spoilerTitle && <div className="color-card">
              {spoilerTitle}
            </div>}
            {infotainmentTitle && <div className="color-card">
              {infotainmentTitle}
            </div>}
            {bodyframesTitle && <div className="color-card">
              {bodyframesTitle}
            </div>}
              <Link className="nice-button" to="/finish">Finished Product</Link>
          </div>
          <div className="dragg">
            <DragDropContext onDragEnd={handleDragEnd}>
              <Droppable droppableId="choices">
                {(provided) => {
                  return (
                    <div ref={provided.innerRef} {...provided.droppableProps} className="droppable-col"> 
                    <SpoilersDraggable >

                    </SpoilersDraggable>
                    <WheelsDraggable></WheelsDraggable>
                    <AirbagsDraggable>

                    </AirbagsDraggable>
                    <BodyFrameDraggable></BodyFrameDraggable>
                    <InfotainmentDraggable></InfotainmentDraggable>

                    </div>)
                }
                }

              </Droppable>
              <Droppable droppableId="box">
                {(provided) =>{
                  return (
                    <div ref={provided.innerRef} {...provided.droppableProps}
                    className="box-col"
                    >
                      Drop the customizable choices here
                    </div>
                  )
                }}
              </Droppable>
            </DragDropContext>
          </div>
          </div>
        </Route>
        <Route path="/finish">
          <div className="assembled-label label">
            You have selected
          </div>
          <div className="assembled-product">
            {wheelTitle && <div className="color-card">
              {wheelTitle}
            </div>}
            {airbagTitle && <div className="color-card">
              {airbagTitle}
            </div>}
            {spoilerTitle && <div className="color-card">
              {spoilerTitle}
            </div>}
            {infotainmentTitle && <div className="color-card">
              {infotainmentTitle}
            </div>}
            {bodyframesTitle && <div className="color-card">
              {bodyframesTitle}
            </div>}
          </div>
          <Link className="nice-button" to="/">
            Home
          </Link>
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
