import {createSlice} from '@reduxjs/toolkit';
export const spoilerSlice = createSlice({
    name: 'spoilers',
    initialState: {
        id: null,
        title: ''
    },
    reducers: {
        addSpoilers: (state, action) => {
            state.id = action.payload.id;
            state.title = action.payload.title;
        },
        removeSpoilers: (state) =>{
            state.id = null;
            state.title = '';
        }
    },
})

export const selectTitle = (state) => state.spoilers.title;

export const selectId = (state) => state.spoilers.id;

const selectors = {
    selectTitle,
    selectId
}

export {selectors};

export const {addSpoilers, removeSpoilers} = spoilerSlice.actions;

export default spoilerSlice.reducer;