import { Droppable, Draggable, DragDropContext } from "react-beautiful-dnd";
import { useSelector } from "react-redux";
import { selectors } from '../wheels/wheelsSlice';
import wheelTypes from "./wheelTypes";
import '../CardView.css';


export default function WheelsDraggable(){
    
    return (
        <div>
            {
                wheelTypes.map((wheelType, index) =>
                (
                    <Draggable key={wheelType.id} draggableId={wheelType.id} index={index}>
                        {(provided) => (
                            <div className="dragging" ref={provided.innerRef} {...provided.draggableProps} {...provided.dragHandleProps}>
                                {wheelType.title}
                            </div>
                        )}

                    </Draggable>
                )
                )
            }
        </div>

    )
}